import React from 'react';
import { Link } from 'gatsby';
import camelCase from '../utils/camelCase';

const TagList = ({ tag, tags, className, max }) => {
  return (
    <React.Fragment>
      {tag && (
        <h1 className="overview__title">{`Selected tag: ${tag.toLowerCase()}`}</h1>
      )}
      <div className={`tags ${className}`}>
        <ul className="tags">
          {tags.slice(0, max).map((t, i) => (
            <li key={i}>
              <Link key={i} to={`/tags/${camelCase(t)}`}>
                {t.toLowerCase()}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </React.Fragment>
  );
};

TagList.defaultProps = {
  className: '',
  max: 100
};

export default TagList;
